.profile {
    grid-area: c;
    background-color: #00264d;
 
} 
  .post{
    margin-top: 15px;
    width: 400px;
    height: justify;
 text-align: left;
 background-color:#ac98ac;
 padding-left: 50px;
 font-size: large;
      }
      .post p{
        margin-bottom: 5px;
        width: 380px;
        height: auto;
        background-color:#ac98ac;
        text-align: justify;
        word-break: initial;
        
      }
    .post img{
      width: 30px;
      height: 30px;
      float: left;
      margin: 5px 10px 5px 0px;
    }
    .button img{
      width: 10px;
      height: 10px;
    }
    .buttons{
      background-color: transparent;
      width: 400px;
      height: 30px;
    }
    
    button{
      margin: 0px 5px 0px 5px;
      font-size: small;
    }
    .like {
      width: 50px;
      height: 30px;
      background-color: green;
      float: right;
    }
    .dislike{
      width: 50px;
      height: 30px;
      background-color: red;
      float: right;
    }
    .comment{
      width: 65px;
      height: 30px;
      background-color: whitesmoke;
      float: left;    
    }
    .repost {
      background-color: whitesmoke;
      width: 50px;
      height: 30px;
      float: left;
    }